import React, { useEffect, useRef } from 'react'
import "../style/Home.css"
import {gsap} from 'gsap'
import {ScrollTrigger} from 'gsap/ScrollTrigger'
import { Power3 } from 'gsap'
import { Linear } from 'gsap'
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';


const Home = () => {


  gsap.registerPlugin(ScrollTrigger);
  const ref = useRef(null);
  // const randomno = Math.floor((Math.random() * 10) + 1); 
  const tl = useRef();
  useEffect(()=>{
    console.log('render')
    const element = ref.current;
    tl.current = gsap.timeline()
    .fromTo(
      element.querySelector(".subcontainer.first"),{
        opacity:0,
        y:-100,
      },{
          opacity:1,
          y:0,
          duration:3,
          ease: Power3.easeOut,         
          delay: 1
          
      })
      .to(element.querySelector(".subcontainer.first"),{
        duration: 90,
        repeat:-1,
        ease: Linear.easeNone,
        backgroundPosition: "-2000px 0px",
        yoyo: 1
      })
      .fromTo(
        element.querySelector(".content2"),{
          opacity:0,
          y: -200,
        },{
            opacity:1,
            y:0,
            scrollTrigger:{
              trigger: ".subcontainer.second",
              start: "top 60%",
              end: "top 0%",
              scrub:true,
              // markers:true,
              toggleActions: "play pause reverse pause"
            },
            ease: Power3.easeOut,
            delay: 0
            
        })
      .fromTo(
          element.querySelector(".content2"),{
            opacity:1,
            y: 0,
            
          },{
              opacity:0,
              y:100,
              scrollTrigger:{
                trigger: ".subcontainer.second",
                start: "bottom 75%",
                end: "bottom  25%",
                scrub:true,
                // markers:true,
                toggleActions: "restart pause reverse pause"
              },
              ease: Power3.easeOut,
              delay: 0
              
          })

      .fromTo(
          element.querySelector(".content3"),{
            opacity:0,
            y:-100,
          },{
              opacity:1,
              y:0,
              duration:5,
              scrollTrigger:{
                trigger: ".subcontainer.third",
                start: "top 60%",
                end: "top 0%",
                scrub:true,
                // markers:true,
                toggleActions: "restart pause reverse pause"
              },
              ease: Power3.easeOut,
              delay: 1
              
          })

     
      
  },[])
  
  

  


  


  return (
    <div className="maincontainer-home" ref={ref}>
      <div className="subcontainer first">
       <div className='content1'>
       Amith Ajith
       </div>
       <div className='subcontent1'>
       Explore
       </div>
       <KeyboardArrowDownIcon/>
     
      
      </div>      
      <div className="subcontainer second">

      <div className='content2'>
        
       Secon Div
       </div>
      </div>

      <div className="subcontainer third">

      <div className='content3'>
       Third Div
       </div>
      </div>
    </div>
  )
}

export default Home